/* ============================================
   SIGNALS VIEW - Premium Widget Dashboard
   ============================================ */

.signals-mesh-container {
    padding: 1.5rem;
    min-height: 100%;
}

.signals-mesh-grid {
    display: grid;
    grid-template-columns: 340px 1fr;
    gap: 1.5rem;
    max-width: 1400px;
    margin: 0 auto;
}

@media (max-width: 1024px) {
    .signals-mesh-grid {
        grid-template-columns: 1fr;
    }
}

.signals-column {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.left-column {
    position: sticky;
    top: 1.5rem;
    height: fit-content;
}

@media (max-width: 1024px) {
    .left-column {
        position: static;
    }
}

/* ============================================
   Widget Base Styles
   ============================================ */

.mesh-widget {
    background: linear-gradient(145deg, rgba(30, 30, 35, 0.9), rgba(20, 20, 24, 0.95));
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 20px;
    padding: 1.5rem;
    backdrop-filter: blur(20px);
    position: relative;
    overflow: hidden;
}

.mesh-widget::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg,
            transparent 0%,
            rgba(255, 255, 255, 0.1) 50%,
            transparent 100%);
}

/* ============================================
   Widget Headers
   ============================================ */

.widget-header-compact {
    display: flex;
    align-items: center;
    gap: 0.625rem;
    margin-bottom: 1rem;
}

.header-icon-small {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    background: rgba(41, 151, 255, 0.1);
    border: 1px solid rgba(41, 151, 255, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--accent, #2997ff);
}

.header-icon-small.green {
    background: rgba(16, 185, 129, 0.1);
    border-color: rgba(16, 185, 129, 0.2);
    color: #10b981;
}

.header-label {
    font-size: 0.65rem;
    font-weight: 600;
    letter-spacing: 0.12em;
    color: rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
}

.widget-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.25rem;
}

.widget-header .header-left {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.widget-header h3 {
    font-size: 0.95rem;
    font-weight: 600;
    color: #fff;
    margin: 0;
}

/* ============================================
   Capital Widget
   ============================================ */

.capital-display {
    display: flex;
    align-items: baseline;
    margin-bottom: 0.75rem;
}

.capital-display .currency {
    font-size: 1.5rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.5);
    margin-right: 2px;
}

.capital-display .amount {
    font-size: 2.25rem;
    font-weight: 800;
    color: #fff;
    font-family: var(--font-jetbrains-mono), monospace;
    letter-spacing: -0.02em;
}

.capital-display .cents {
    font-size: 1.25rem;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
}

.capital-change {
    display: flex;
    align-items: center;
    gap: 0.375rem;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.5);
}

.capital-change.positive {
    color: #10b981;
}

.capital-change.positive svg {
    color: #10b981;
}

/* ============================================
   P/L Widget
   ============================================ */

.pl-display {
    font-size: 2rem;
    font-weight: 800;
    font-family: var(--font-jetbrains-mono), monospace;
    letter-spacing: -0.02em;
    margin-bottom: 0.75rem;
}

.pl-display.positive {
    color: #10b981;
}

.pl-display.negative {
    color: #ef4444;
}

.pl-sessions {
    display: flex;
    align-items: baseline;
    gap: 0.5rem;
}

.pl-sessions .session-count {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--accent, #2997ff);
}

.pl-sessions .session-label {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
}

/* ============================================
   Status Widget
   ============================================ */

.signal-indicator {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: rgba(168, 85, 247, 0.15);
    border: 1px solid rgba(168, 85, 247, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #a855f7;
    position: relative;
}

.signal-indicator::after {
    content: '';
    position: absolute;
    inset: -4px;
    border-radius: 50%;
    border: 1px solid rgba(168, 85, 247, 0.2);
    animation: signalPing 2s ease-out infinite;
}

@keyframes signalPing {
    0% {
        transform: scale(1);
        opacity: 0.5;
    }

    100% {
        transform: scale(1.5);
        opacity: 0;
    }
}

.status-badge {
    font-size: 0.6rem;
    font-weight: 700;
    letter-spacing: 0.1em;
    padding: 0.25rem 0.625rem;
    border-radius: 6px;
    text-transform: uppercase;
}

.status-badge.live {
    background: rgba(16, 185, 129, 0.15);
    border: 1px solid rgba(16, 185, 129, 0.3);
    color: #10b981;
}

.status-metrics {
    display: flex;
    flex-direction: column;
    gap: 0.875rem;
    margin-bottom: 1.25rem;
}

.status-metrics .metric {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
}

.status-metrics .metric-content {
    display: flex;
    flex-direction: column;
}

.status-metrics .metric-value {
    font-size: 1rem;
    font-weight: 700;
    color: #fff;
    font-family: var(--font-jetbrains-mono), monospace;
}

.status-metrics .metric-value.positive {
    color: #10b981;
}

.status-metrics .metric-label {
    font-size: 0.65rem;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.connection-status {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.06);
}

.connection-indicator {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.5);
}

.pulse-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #10b981;
    animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {

    0%,
    100% {
        opacity: 1;
        box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.4);
    }

    50% {
        opacity: 0.8;
        box-shadow: 0 0 0 8px rgba(16, 185, 129, 0);
    }
}

.latency {
    font-size: 0.7rem;
    font-family: var(--font-jetbrains-mono), monospace;
    color: #10b981;
}

/* ============================================
   Filter Widget
   ============================================ */

.category-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.category-btn {
    padding: 0.5rem 1rem;
    border-radius: 10px;
    font-size: 0.7rem;
    font-weight: 600;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.08);
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    transition: all 0.2s ease;
}

.category-btn:hover {
    background: rgba(255, 255, 255, 0.06);
    border-color: rgba(255, 255, 255, 0.12);
    color: rgba(255, 255, 255, 0.8);
}

.category-btn.active {
    background: var(--accent, #2997ff);
    border-color: var(--accent, #2997ff);
    color: #fff;
    box-shadow: 0 0 20px rgba(41, 151, 255, 0.3);
}

/* ============================================
   Signals Header & List
   ============================================ */

.signals-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.signals-header h2 {
    font-size: 1.25rem;
    font-weight: 700;
    color: #fff;
    margin: 0;
}

.signal-count {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    font-family: var(--font-jetbrains-mono), monospace;
}

.signals-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

/* ============================================
   Signal Card Widget
   ============================================ */

.signal-card-widget {
    background: linear-gradient(145deg, rgba(30, 30, 35, 0.9), rgba(20, 20, 24, 0.95));
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 20px;
    padding: 1.5rem;
    backdrop-filter: blur(20px);
    position: relative;
    overflow: hidden;
}

.signal-card-widget::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(90deg,
            transparent 0%,
            rgba(255, 255, 255, 0.1) 50%,
            transparent 100%);
}

/* Signal Card Header */
.signal-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1.25rem;
}

.provider-info {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.provider-avatar {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    background: rgba(41, 151, 255, 0.1);
    border: 1px solid rgba(41, 151, 255, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--accent, #2997ff);
}

.provider-details {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.provider-name-row {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.provider-name {
    font-size: 0.95rem;
    font-weight: 600;
    color: #fff;
}

.provider-rank {
    font-size: 0.55rem;
    font-weight: 700;
    letter-spacing: 0.08em;
    padding: 0.2rem 0.5rem;
    border-radius: 4px;
    text-transform: uppercase;
}

.provider-rank.elite {
    background: linear-gradient(135deg, rgba(251, 191, 36, 0.2), rgba(245, 158, 11, 0.1));
    border: 1px solid rgba(251, 191, 36, 0.3);
    color: #fbbf24;
}

.provider-rank.pro {
    background: rgba(41, 151, 255, 0.1);
    border: 1px solid rgba(41, 151, 255, 0.2);
    color: var(--accent, #2997ff);
}

.provider-meta {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
}

.provider-meta svg {
    opacity: 0.6;
}

.win-rate {
    display: flex;
    align-items: center;
    gap: 0.25rem;
    color: #fbbf24;
}

.pair-info {
    text-align: right;
}

.pair-name {
    display: block;
    font-size: 1.375rem;
    font-weight: 800;
    color: var(--accent, #2997ff);
    font-style: italic;
    letter-spacing: -0.02em;
    margin-bottom: 0.25rem;
}

.action-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
    font-size: 0.65rem;
    font-weight: 700;
    letter-spacing: 0.08em;
    padding: 0.25rem 0.5rem;
    border-radius: 6px;
    text-transform: uppercase;
}

.action-badge.buy {
    background: rgba(16, 185, 129, 0.15);
    border: 1px solid rgba(16, 185, 129, 0.3);
    color: #10b981;
}

.action-badge.sell {
    background: rgba(239, 68, 68, 0.15);
    border: 1px solid rgba(239, 68, 68, 0.3);
    color: #ef4444;
}

/* Signal Metrics */
.signal-metrics {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.75rem;
    margin-bottom: 1.25rem;
}

.metric-box {
    background: rgba(10, 10, 12, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 12px;
    padding: 0.875rem;
    text-align: center;
}

.metric-box .metric-label {
    display: block;
    font-size: 0.55rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    margin-bottom: 0.375rem;
}

.metric-box .metric-value {
    font-size: 0.95rem;
    font-weight: 600;
    color: #fff;
    font-family: var(--font-jetbrains-mono), monospace;
}

.metric-box.sl .metric-label {
    color: rgba(239, 68, 68, 0.7);
}

.metric-box.sl .metric-value {
    color: #ef4444;
}

.metric-box.tp .metric-label {
    color: rgba(16, 185, 129, 0.7);
}

.metric-box.tp .metric-value {
    color: #10b981;
}

/* Signal Profit */
.signal-profit {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.25rem;
}

.profit-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.profit-label {
    font-size: 0.65rem;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    letter-spacing: 0.08em;
}

.profit-value {
    font-size: 1.5rem;
    font-weight: 700;
    font-family: var(--font-jetbrains-mono), monospace;
}

.profit-value.positive {
    color: #10b981;
}

.profit-value.negative {
    color: #ef4444;
}

.shield-icon {
    color: rgba(41, 151, 255, 0.3);
}

/* ============================================
   Swipe to Approve Button
   ============================================ */

.swipe-container {
    position: relative;
    overflow: hidden;
    border-radius: 14px;
}

.swipe-track {
    position: relative;
    height: 52px;
    background: linear-gradient(135deg, rgba(16, 185, 129, 0.15), rgba(16, 185, 129, 0.08));
    border: 1px solid rgba(16, 185, 129, 0.3);
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.swipe-track.approved {
    background: linear-gradient(135deg, rgba(16, 185, 129, 0.25), rgba(16, 185, 129, 0.15));
    border-color: rgba(16, 185, 129, 0.5);
}

.swipe-thumb {
    position: absolute;
    left: 4px;
    width: 44px;
    height: 44px;
    background: linear-gradient(135deg, #10b981, #059669);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    cursor: grab;
    z-index: 10;
    box-shadow:
        0 4px 15px rgba(16, 185, 129, 0.4),
        0 0 30px rgba(16, 185, 129, 0.2);
}

.swipe-thumb:active {
    cursor: grabbing;
}

.swipe-text {
    font-size: 0.75rem;
    font-weight: 700;
    letter-spacing: 0.1em;
    color: rgba(255, 255, 255, 0.85);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.swipe-arrow {
    color: rgba(255, 255, 255, 0.4);
    animation: arrowPulse 1.5s ease-in-out infinite;
}

@keyframes arrowPulse {

    0%,
    100% {
        opacity: 0.4;
        transform: translateX(0);
    }

    50% {
        opacity: 0.8;
        transform: translateX(4px);
    }
}

.approved-icon {
    color: #10b981;
    margin-right: 0.5rem;
}

/* ============================================
   Empty State
   ============================================ */

.empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4rem 2rem;
    text-align: center;
    opacity: 0.5;
}

.empty-state svg {
    color: rgba(255, 255, 255, 0.3);
    margin-bottom: 1rem;
}

.empty-state h3 {
    font-size: 1.125rem;
    font-weight: 600;
    color: #fff;
    margin: 0 0 0.5rem 0;
}

.empty-state p {
    font-size: 0.875rem;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
    max-width: 300px;
}

/* ============================================
   Responsive Adjustments
   ============================================ */

@media (max-width: 768px) {
    .signals-mesh-container {
        padding: 1rem;
    }

    .signal-metrics {
        grid-template-columns: 1fr;
        gap: 0.5rem;
    }

    .metric-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        text-align: left;
        padding: 0.75rem 1rem;
    }

    .metric-box .metric-label {
        margin-bottom: 0;
    }
}

/* ============================================
   MT5 Connection Modal
   ============================================ */


/* ============================================
   Results & Analytics Tabs
   ============================================ */

.signals-tabs {
    display: flex;
    gap: 1rem;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
    padding-bottom: 1rem;
}

.tab-btn {
    background: transparent;
    border: none;
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.9rem;
    font-weight: 600;
    padding: 0.5rem 1rem;
    cursor: pointer;
    position: relative;
    transition: all 0.2s ease;
}

.tab-btn:hover {
    color: #fff;
}

.tab-btn.active {
    color: #fff;
}

.tab-btn.active::after {
    content: '';
    position: absolute;
    bottom: -1.1rem;
    left: 0;
    width: 100%;
    height: 2px;
    background: var(--accent, #2997ff);
    box-shadow: 0 -4px 10px rgba(41, 151, 255, 0.5);
    border-radius: 2px;
}

/* ============================================
   Result Card Styles
   ============================================ */

.result-card {
    border-left: 4px solid transparent;
}

.result-card.profit {
    border-left-color: #10b981;
    background: linear-gradient(145deg, rgba(16, 185, 129, 0.05), rgba(20, 20, 24, 0.95));
}

.result-card.loss {
    border-left-color: #ef4444;
    background: linear-gradient(145deg, rgba(239, 68, 68, 0.05), rgba(20, 20, 24, 0.95));
}

.result-card .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}

.result-card .signal-type {
    font-size: 0.7rem;
    font-weight: 800;
    padding: 0.2rem 0.6rem;
    border-radius: 4px;
    margin-left: 0.8rem;
}

.result-card .signal-type.buy {
    color: #10b981;
    background: rgba(16, 185, 129, 0.1);
}

.result-card .signal-type.sell {
    color: #ef4444;
    background: rgba(239, 68, 68, 0.1);
}

.result-card .card-body {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.result-card .price-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgba(255, 255, 255, 0.03);
    padding: 0.8rem;
    border-radius: 8px;
}

.result-card .price-item {
    display: flex;
    flex-direction: column;
}

.result-card .price-item .label {
    font-size: 0.6rem;
    color: rgba(255, 255, 255, 0.4);
    margin-bottom: 0.2rem;
}

.result-card .price-item .value {
    font-size: 0.9rem;
    font-weight: 700;
    color: #fff;
    font-family: var(--font-jetbrains-mono), monospace;
}

.result-card .arrow {
    color: rgba(255, 255, 255, 0.2);
    font-weight: 300;
}

.result-card .result-row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.result-card .pnl-container {
    display: flex;
    flex-direction: column;
}

.result-card .pnl-label {
    font-size: 0.7rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: rgba(255, 255, 255, 0.4);
    margin-bottom: 0.4rem;
}

.result-card .pnl-value {
    font-size: 1.8rem;
    font-weight: 800;
    letter-spacing: -0.02em;
    font-family: var(--font-jetbrains-mono), monospace;
}

.result-card .pnl-value.green {
    color: #10b981;
    text-shadow: 0 0 20px rgba(16, 185, 129, 0.3);
}

.result-card .pnl-value.red {
    color: #ef4444;
    text-shadow: 0 0 20px rgba(239, 68, 68, 0.3);
}

.result-card .status-icon {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.result-card .status-icon.win {
    background: rgba(16, 185, 129, 0.1);
    color: #10b981;
}

.result-card .status-icon.loss {
    background: rgba(239, 68, 68, 0.1);
    color: #ef4444;
}

.result-card .card-footer {
    border-top: 1px solid rgba(255, 255, 255, 0.05);
    margin-top: 1rem;
    padding-top: 0.8rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
}

.result-card .meta-info {
    display: flex;
    align-items: center;
    gap: 0.4rem;
}

.result-analytics-header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    margin-bottom: 1.5rem;
}

/* ============================================
   MT5 Connection Modal
   ============================================ */

.mt5-modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.85);
    backdrop-filter: blur(10px);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
}

.mt5-modal {
    position: relative;
    max-width: 420px;
    width: 100%;
    padding: 2.5rem;
    text-align: center;
}

.mt5-modal .modal-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    transition: all 0.2s ease;
}

.mt5-modal .modal-close:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #fff;
}

.mt5-modal .modal-icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 1.5rem;
    background: linear-gradient(135deg, rgba(41, 151, 255, 0.2), rgba(41, 151, 255, 0.05));
    border: 1px solid rgba(41, 151, 255, 0.3);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--accent, #2997ff);
}

.mt5-modal h2 {
    font-size: 1.5rem;
    font-weight: 700;
    margin: 0 0 0.5rem 0;
    color: #fff;
}

.mt5-modal>p {
    font-size: 0.875rem;
    color: rgba(255, 255, 255, 0.5);
    margin: 0 0 2rem 0;
}

.mt5-modal .modal-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.mt5-modal .input-group {
    text-align: left;
}

.mt5-modal .input-group label {
    display: block;
    font-size: 0.65rem;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
    margin-bottom: 0.5rem;
}

.mt5-modal .input-group input,
.mt5-modal .input-group select {
    width: 100%;
    padding: 0.875rem 1rem;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    color: #fff;
    font-size: 0.95rem;
    transition: all 0.2s ease;
}

.mt5-modal .input-group input:focus,
.mt5-modal .input-group select:focus {
    outline: none;
    border-color: var(--accent, #2997ff);
    background: rgba(41, 151, 255, 0.05);
}

.mt5-modal .input-group input::placeholder {
    color: rgba(255, 255, 255, 0.3);
}

.mt5-modal .btn-primary {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 1rem;
    background: var(--accent, #2997ff);
    border: none;
    border-radius: 12px;
    color: #fff;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
    margin-top: 0.5rem;
    transition: all 0.2s ease;
}

.mt5-modal .btn-primary:hover:not(:disabled) {
    background: #1e7ce8;
    transform: translateY(-1px);
}

.mt5-modal .btn-primary:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

/* ============================================
   MT5 Connection Widget
   ============================================ */

.connection-widget {
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.3s ease;
}

.connection-widget.connected {
    border-color: rgba(16, 185, 129, 0.3);
    background: linear-gradient(145deg, rgba(16, 185, 129, 0.08), rgba(20, 20, 24, 0.95));
}

.connection-widget .connection-icon {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(255, 255, 255, 0.5);
    transition: all 0.3s ease;
}

.connection-widget .connection-icon.active {
    background: rgba(16, 185, 129, 0.15);
    border-color: rgba(16, 185, 129, 0.3);
    color: #10b981;
}

.connection-widget .status-badge.offline {
    background: rgba(239, 68, 68, 0.15);
    border: 1px solid rgba(239, 68, 68, 0.3);
    color: #ef4444;
}

.connection-details {
    margin-top: 1rem;
}

.connection-details .account-stats {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0.75rem;
    margin-bottom: 1rem;
}

.connection-details .stat {
    background: rgba(10, 10, 12, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    padding: 0.75rem;
    text-align: center;
}

.connection-details .stat .label {
    display: block;
    font-size: 0.6rem;
    font-weight: 600;
    letter-spacing: 0.08em;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    margin-bottom: 0.25rem;
}

.connection-details .stat .value {
    font-size: 1rem;
    font-weight: 700;
    color: #fff;
    font-family: var(--font-jetbrains-mono), monospace;
}

.connection-details .connection-info {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.5);
    margin-bottom: 1rem;
}

.connection-details .btn-disconnect {
    width: 100%;
    padding: 0.75rem;
    background: rgba(239, 68, 68, 0.1);
    border: 1px solid rgba(239, 68, 68, 0.3);
    border-radius: 10px;
    color: #ef4444;
    font-size: 0.75rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
}

.connection-details .btn-disconnect:hover {
    background: rgba(239, 68, 68, 0.2);
}

.connection-prompt {
    margin-top: 0.5rem;
}

.connection-prompt p {
    font-size: 0.8rem;
    color: rgba(255, 255, 255, 0.5);
    margin: 0 0 1rem 0;
    line-height: 1.5;
}

.connection-prompt .btn-connect {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.875rem;
    background: var(--accent, #2997ff);
    border: none;
    border-radius: 10px;
    color: #fff;
    font-size: 0.85rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
}

.connection-prompt .btn-connect:hover {
    background: #1e7ce8;
    transform: translateY(-1px);
}

/* ============================================
   Connection Warning Banner
   ============================================ */

.connection-warning {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 1.25rem;
    background: linear-gradient(135deg, rgba(251, 191, 36, 0.1), rgba(251, 191, 36, 0.05));
    border: 1px solid rgba(251, 191, 36, 0.3);
    border-radius: 12px;
    margin-bottom: 1rem;
}

.connection-warning svg {
    color: #fbbf24;
    flex-shrink: 0;
}

.connection-warning span {
    font-size: 0.85rem;
    color: rgba(255, 255, 255, 0.8);
    flex: 1;
}

.connection-warning button {
    background: rgba(251, 191, 36, 0.2);
    border: 1px solid rgba(251, 191, 36, 0.4);
    border-radius: 8px;
    padding: 0.5rem 1rem;
    color: #fbbf24;
    font-size: 0.75rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    white-space: nowrap;
}

.connection-warning button:hover {
    background: rgba(251, 191, 36, 0.3);
}

/* ============================================
   Signal Card - Multi-TP Layout
   ============================================ */

.signal-metrics.expanded {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.signal-metrics .metric-row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.75rem;
}

.signal-metrics .tp-row .metric-box {
    background: rgba(16, 185, 129, 0.05);
    border-color: rgba(16, 185, 129, 0.15);
}

.metric-box.entry .metric-label {
    color: rgba(41, 151, 255, 0.7);
}

.metric-box.entry .metric-value {
    color: var(--accent, #2997ff);
}

.metric-box.lot .metric-label {
    color: rgba(168, 85, 247, 0.7);
}

.metric-box.lot .metric-value {
    color: #a855f7;
}

/* ============================================
   Disabled Signal Card State
   ============================================ */

.signal-card-widget.disabled {
    opacity: 0.7;
}

.swipe-track.disabled {
    background: linear-gradient(135deg, rgba(100, 100, 110, 0.15), rgba(100, 100, 110, 0.08));
    border-color: rgba(100, 100, 110, 0.3);
}

.swipe-track.disabled .swipe-thumb {
    background: linear-gradient(135deg, #6b7280, #4b5563);
    box-shadow:
        0 4px 15px rgba(100, 100, 120, 0.3),
        0 0 20px rgba(100, 100, 120, 0.1);
}

.swipe-track.disabled .swipe-text {
    color: rgba(255, 255, 255, 0.5);
}

/* ============================================
   Responsive Adjustments for New Elements
   ============================================ */

@media (max-width: 768px) {
    .signal-metrics .metric-row {
        grid-template-columns: 1fr;
    }

    .connection-details .account-stats {
        grid-template-columns: 1fr;
    }

    .connection-warning {
        flex-wrap: wrap;
    }

    .connection-warning button {
        width: 100%;
        margin-top: 0.5rem;
    }
}

/* ============================================
   DxTrade Specific Styles
   ============================================ */

/* DxTrade Modal Accent */
.mt5-modal.dxtrade-modal .modal-icon.dxtrade {
    background: linear-gradient(135deg, rgba(245, 158, 11, 0.2), rgba(245, 158, 11, 0.05));
    border-color: rgba(245, 158, 11, 0.3);
    color: #f59e0b;
}

.us30-notice {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1rem;
    background: rgba(245, 158, 11, 0.08);
    border: 1px solid rgba(245, 158, 11, 0.2);
    border-radius: 8px;
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.8rem;
}

.us30-notice svg {
    color: #f59e0b;
    flex-shrink: 0;
}

.mt5-modal .btn-primary.dxtrade-btn {
    background: linear-gradient(135deg, #f59e0b, #d97706);
}

.mt5-modal .btn-primary.dxtrade-btn:hover:not(:disabled) {
    background: linear-gradient(135deg, #d97706, #b45309);
}

/* DxTrade Widget */
.dxtrade-widget .connection-icon.dxtrade {
    background: rgba(245, 158, 11, 0.1);
    border-color: rgba(245, 158, 11, 0.2);
    color: #f59e0b;
}

.dxtrade-widget .connection-icon.dxtrade.active {
    background: rgba(245, 158, 11, 0.15);
    border-color: rgba(245, 158, 11, 0.3);
    color: #f59e0b;
}

.dxtrade-widget.connected {
    border-color: rgba(245, 158, 11, 0.3);
    background: linear-gradient(145deg, rgba(245, 158, 11, 0.08), rgba(20, 20, 24, 0.95));
}

.us30-tag {
    font-size: 0.55rem;
    font-weight: 700;
    letter-spacing: 0.08em;
    padding: 0.15rem 0.4rem;
    background: rgba(245, 158, 11, 0.15);
    border: 1px solid rgba(245, 158, 11, 0.3);
    border-radius: 4px;
    color: #f59e0b;
    margin-left: 0.5rem;
    vertical-align: middle;
}

.btn-connect.dxtrade {
    background: linear-gradient(135deg, #f59e0b, #d97706);
}

.btn-connect.dxtrade:hover {
    background: linear-gradient(135deg, #d97706, #b45309);
}

.stat.full-width {
    grid-column: span 2;
}

.stat .value.mono {
    font-family: var(--font-jetbrains-mono), monospace;
    letter-spacing: 0.05em;
}

/* Connection Warning with Multiple Buttons */
.connection-warning .warning-buttons {
    display: flex;
    gap: 0.5rem;
}

.connection-warning .warning-buttons button {
    padding: 0.5rem 0.875rem;
}

.connection-warning .warning-buttons button.dxtrade {
    background: rgba(245, 158, 11, 0.2);
    border-color: rgba(245, 158, 11, 0.4);
    color: #f59e0b;
}

.connection-warning .warning-buttons button.dxtrade:hover {
    background: rgba(245, 158, 11, 0.3);
}

/* US30 Signal Card */
.signal-card-widget.us30-signal {
    position: relative;
}

.dxtrade-execution-badge {
    position: absolute;
    top: 0.75rem;
    right: 0.75rem;
    display: flex;
    align-items: center;
    gap: 0.35rem;
    padding: 0.25rem 0.5rem;
    background: linear-gradient(135deg, rgba(245, 158, 11, 0.2), rgba(245, 158, 11, 0.1));
    border: 1px solid rgba(245, 158, 11, 0.3);
    border-radius: 6px;
    font-size: 0.6rem;
    font-weight: 700;
    letter-spacing: 0.08em;
    color: #f59e0b;
    z-index: 5;
}

.pair-name.us30 {
    color: #f59e0b;
}

/* Swipe Track DxTrade variant */
.swipe-track.dxtrade {
    background: linear-gradient(135deg, rgba(245, 158, 11, 0.15), rgba(245, 158, 11, 0.08));
    border-color: rgba(245, 158, 11, 0.3);
}

.swipe-track.dxtrade .swipe-thumb {
    background: linear-gradient(135deg, #f59e0b, #d97706);
    box-shadow:
        0 4px 15px rgba(245, 158, 11, 0.4),
        0 0 30px rgba(245, 158, 11, 0.2);
}

.swipe-track.approved.dxtrade {
    background: linear-gradient(135deg, rgba(245, 158, 11, 0.25), rgba(245, 158, 11, 0.15));
    border-color: rgba(245, 158, 11, 0.5);
}

.swipe-track.approved.dxtrade .approved-icon {
    color: #f59e0b;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    .connection-warning .warning-buttons {
        width: 100%;
    }

    .connection-warning .warning-buttons button {
        flex: 1;
    }
}

/* ============================================
   DxTrade Account Selection Styles
   ============================================ */

.mt5-modal.accounts-step {
    max-width: 480px;
}

.modal-icon.dxtrade.success {
    background: linear-gradient(135deg, rgba(34, 197, 94, 0.2), rgba(34, 197, 94, 0.05));
    border-color: rgba(34, 197, 94, 0.3);
    color: #22c55e;
}

.accounts-list {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-bottom: 1.25rem;
    max-height: 280px;
    overflow-y: auto;
}

.account-card {
    position: relative;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 12px;
    padding: 1rem 1.25rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.account-card:hover {
    background: rgba(255, 255, 255, 0.06);
    border-color: rgba(255, 255, 255, 0.12);
}

.account-card.selected {
    background: rgba(245, 158, 11, 0.1);
    border-color: rgba(245, 158, 11, 0.4);
}

.account-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 0.75rem;
}

.account-info {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.account-name {
    font-weight: 600;
    color: rgba(255, 255, 255, 0.95);
    font-size: 0.95rem;
}

.account-id {
    font-family: var(--font-jetbrains-mono), monospace;
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 0.05em;
}

.account-type {
    font-size: 0.65rem;
    font-weight: 700;
    letter-spacing: 0.08em;
    padding: 0.25rem 0.5rem;
    border-radius: 6px;
    text-transform: uppercase;
}

.account-type.challenge {
    background: rgba(139, 92, 246, 0.15);
    border: 1px solid rgba(139, 92, 246, 0.3);
    color: #a78bfa;
}

.account-type.funded {
    background: rgba(34, 197, 94, 0.15);
    border: 1px solid rgba(34, 197, 94, 0.3);
    color: #22c55e;
}

.account-type.demo {
    background: rgba(100, 116, 139, 0.15);
    border: 1px solid rgba(100, 116, 139, 0.3);
    color: #94a3b8;
}

.account-balance {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.balance-label {
    font-size: 0.7rem;
    color: rgba(255, 255, 255, 0.4);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.balance-value {
    font-family: var(--font-jetbrains-mono), monospace;
    font-size: 1.1rem;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.95);
}

.selected-indicator {
    position: absolute;
    top: 50%;
    right: 1rem;
    transform: translateY(-50%);
    color: #f59e0b;
}

.btn-secondary {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.875rem 1.5rem;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.12);
    border-radius: 10px;
    color: rgba(255, 255, 255, 0.6);
    font-weight: 600;
    font-size: 0.85rem;
    cursor: pointer;
    transition: all 0.2s ease;
    margin-top: 0.5rem;
}

.btn-secondary:hover {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.2);
    color: rgba(255, 255, 255, 0.8);
}

.btn-primary:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Scrollbar for accounts list */
.accounts-list::-webkit-scrollbar {
    width: 6px;
}

.accounts-list::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.03);
    border-radius: 3px;
}

.accounts-list::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
}

.accounts-list::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.15);
}

/* ============================================
   Equity Row in Capital Widget
   ============================================ */

.equity-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    border-top: 1px solid rgba(255, 255, 255, 0.06);
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
    margin: 0.5rem 0;
}

.equity-label {
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.equity-value {
    font-size: 0.9rem;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.9);
}

/* ============================================
   Analytics Widget
   ============================================ */

.analytics-widget {
    padding: 1.25rem;
}

.header-icon-small.blue {
    background: rgba(59, 130, 246, 0.1);
    border-color: rgba(59, 130, 246, 0.2);
    color: #3b82f6;
}

.header-icon-small.red {
    background: rgba(239, 68, 68, 0.1);
    border-color: rgba(239, 68, 68, 0.2);
    color: #ef4444;
}

.analytics-buttons {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.analytics-btn {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.875rem 1rem;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 12px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 0.85rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    text-align: left;
}

.analytics-btn span {
    flex: 1;
}

.analytics-btn svg:last-child {
    opacity: 0.5;
    transition: all 0.2s ease;
}

.analytics-btn.active:hover {
    background: rgba(255, 255, 255, 0.06);
    border-color: rgba(255, 255, 255, 0.15);
    transform: translateX(2px);
}

.analytics-btn.active:hover svg:last-child {
    opacity: 1;
    transform: translateX(4px);
}

.analytics-btn.dxtrade.active {
    border-color: rgba(255, 149, 0, 0.3);
}

.analytics-btn.dxtrade.active:hover {
    background: rgba(255, 149, 0, 0.1);
    border-color: rgba(255, 149, 0, 0.4);
}

.analytics-btn.dxtrade.active svg:first-child {
    color: #ff9500;
}

.analytics-btn.mt5.active {
    border-color: rgba(41, 151, 255, 0.3);
}

.analytics-btn.mt5.active:hover {
    background: rgba(41, 151, 255, 0.1);
    border-color: rgba(41, 151, 255, 0.4);
}

.analytics-btn.mt5.active svg:first-child {
    color: #2997ff;
}

.analytics-btn.disabled {
    opacity: 0.4;
    cursor: not-allowed;
}

.analytics-btn.disabled:hover {
    transform: none;
    background: rgba(255, 255, 255, 0.03);
}

.analytics-hint {
    margin: 0.75rem 0 0;
    padding: 0.75rem;
    background: rgba(255, 255, 255, 0.02);
    border-radius: 8px;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
    text-align: center;
}

/* ============================================
   Analytics Modal
   ============================================ */

.analytics-modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.85);
    backdrop-filter: blur(10px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    padding: 2rem;
}

.analytics-modal {
    position: relative;
    width: 100%;
    max-width: 600px;
    max-height: 90vh;
    overflow-y: auto;
    background: linear-gradient(145deg, rgba(35, 35, 40, 0.98), rgba(25, 25, 30, 0.98));
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 24px;
    padding: 2rem;
}

.analytics-modal::-webkit-scrollbar {
    width: 6px;
}

.analytics-modal::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.03);
}

.analytics-modal::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
}

.analytics-modal-header {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.analytics-icon {
    width: 56px;
    height: 56px;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.analytics-icon.dxtrade {
