/* ============================================
   NEW PREMIUM DESIGN TOKENS (REDESIGN)
   ============================================ */

:root {
    --verstige-primary: #ec5b13;
    --verstige-bg-dark: #0a0a0a;
    --verstige-blue-gradient: linear-gradient(135deg, #1e3a8a 0%, #1e40af 50%, #1e3a8a 100%);
    --glass-bg: rgba(255, 255, 255, 0.03);
    --glass-border: rgba(255, 255, 255, 0.1);
    --glass-blur: blur(12px);
}

.bg-premium-blue {
    background: var(--verstige-blue-gradient);
    box-shadow: 0 10px 30px -10px rgba(37, 99, 235, 0.5);
}

.glass-premium {
    background: var(--glass-bg);
    backdrop-filter: var(--glass-blur);
    -webkit-backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
}

.glow-accent {
    box-shadow: 0 0 20px rgba(236, 91, 19, 0.15);
}

.active-glow-blue {
    background: linear-gradient(90deg, #2563eb, #3b82f6);
    box-shadow: 0 0 15px rgba(59, 130, 246, 0.6);
}

/* Redesigned Trade Card */
.trade-card-v2 {
    background: var(--glass-bg);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: 2.5rem;
    padding: 2rem;
    margin-bottom: 1rem;
    position: relative;
    overflow: hidden;
}

.trade-card-v2 .symbol-avatar {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    background: rgba(15, 23, 42, 0.5);
}

.status-bar-v2 {
    background: rgba(37, 99, 235, 0.1);
    border: 1px solid rgba(37, 99, 235, 0.1);
    border-radius: 9999px;
    padding: 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    margin-bottom: 2.5rem;
}

.swipe-container-v2 {
    background: rgba(30, 41, 59, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 9999px;
    padding: 0.5rem;
    height: 64px;
    position: relative;
    display: flex;
    align-items: center;
}

.swipe-button-v2 {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: linear-gradient(90deg, #2563eb, #3b82f6);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 15px rgba(59, 130, 246, 0.6);
    cursor: pointer;
    z-index: 10;
}

/* History Styles */
.history-item-v2 {
    background: var(--glass-bg);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: 1.5rem;
    padding: 1.25rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: transform 0.2s ease;
}

.history-item-v2:hover {
    transform: translateX(4px);
    background: rgba(255, 255, 255, 0.05);
}

/* Connection Modal Styles */
.platform-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
    margin-bottom: 2rem;
}

.platform-option {
    position: relative;
    cursor: pointer;
}

.platform-card {
    background: var(--glass-bg);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: 0.75rem;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    height: 100%;
}

.platform-card.selected {
    border-color: #ec5b13;
    background: rgba(236, 91, 19, 0.05);
}

.platform-icon-box {
    width: 48px;
    height: 48px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0.75rem;
}

.input-glass-v2 {
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 0.75rem;
    padding: 1rem 1rem 1rem 3rem;
    font-size: 0.875rem;
    transition: all 0.3s ease;
}

.input-glass-v2:focus {
    border-color: rgba(236, 91, 19, 0.5);
    background: rgba(255, 255, 255, 0.08);
}

.btn-authorize-v2 {
    width: 100%;
    background: #ec5b13;
    hover: background: #d45111;
    color: white;
    font-weight: 700;
    padding: 1rem;
    border-radius: 0.75rem;
    box-shadow: 0 10px 15px -3px rgba(236, 91, 19, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    transition: all 0.2s ease;
}

.btn-authorize-v2:active {
    transform: scale(0.98);
}
